import React, { Component } from 'react';
import './App.css';

class Camarilla extends Component {

render() {
  return (
    <div className="">
      <div className='cell title'> Camarilla  </div> 
      <div className='cell resistance'> ${   (parseFloat(this.props.state.close) + (parseFloat(this.props.state.range))*1.1/4).toFixed(2) }</div>  
      <div className='cell resistance'> ${   (parseFloat(this.props.state.close) + (parseFloat(this.props.state.range))*1.1/6).toFixed(2) }</div>
      <div className='cell resistance'> ${   (parseFloat(this.props.state.close) + (parseFloat(this.props.state.range))*1.1/12).toFixed(2) } </div>
      <div className='cell pivot'> -- </div>
      <div className='cell support'> ${   (parseFloat(this.props.state.close) - (parseFloat(this.props.state.range))*1.1/12).toFixed(2) } </div>
      <div className='cell support'> ${   (parseFloat(this.props.state.close) - (parseFloat(this.props.state.range))*1.1/6).toFixed(2) } </div>
      <div className='cell support'> ${   (parseFloat(this.props.state.close) - (parseFloat(this.props.state.range))*1.1/4).toFixed(2) } </div> 
    </div>
  );
}
}

export default Camarilla;



